.mobile-slider {
  display: none;
}
.btn-prev,
.btn-next {
  position: absolute;
  cursor: pointer;
  color: hsl(220, 13%, 13%);
  border: none;
  border-radius: 100%;
  background-color: var(--white);
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.btn-prev:hover,
.btn-next:hover {
  color: var(--orange);
}
.btn-prev {
  top: 265px;
  left: 20px;
  padding-left: 15px;
}
.btn-next {
  top: 260px;
  right: 20px;
}
.mobile-image img {
  width: 100%;
  height: 21rem;
}
@media only screen and (max-width: 375px) {
  .mobile-slider {
    display: block;
  }
}
