/* Page Gallery */
.page-gallery {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}
.large-image {
  width: 70%;
  border-radius: 7px;
}
.galleryWrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
}
.galleryWrap .thumb {
  cursor: pointer;
  width: 5rem;
  border-radius: 7px;
}
.galleryWrap img:hover {
  background-color: var(--white);
  opacity: 0.5;
  /* border: 2px solid var(--orange); */
}
