@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");

* {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app {
  display: grid;
  grid-template-columns: auto;
  place-content: center;
  width: 1440px !important;
  font-family: "Kumbh Sans";
}
